/**
 * 接口域名的管理
 */

// 环境的切换
let http = "http://"
let baseDomain = 'localhost';
if (process.env.NODE_ENV == 'development') {
    baseDomain =  'localhost:90';//localhost:90/192.168.2.210
} else if (process.env.NODE_ENV == 'production') {
    http = "//"
    baseDomain =  window.location.host;
}

const base = {
    url: {
        auth: http+baseDomain + '/api/v1',
        core: http+baseDomain + '/api/v1',
        ws: "ws://"+baseDomain,
    },
}

export default base;
