/**
 * 文章发表模块接口列表
 */
import base from './base'; // 导入接口域名列表
import http from '../utils/http'; // 导入http中创建的axios实例

const test = {
    async getPCMList(param) {
        try {
            const res = await http.get(`${base.url.core}/test/list_pcm`, {
                params:param
            });
            if (res.data) {
                return res.data.result;
            }
        }
        catch (err) {
            return Promise.reject(err);
        }
        
    },
    async fileASR(params) {
        try {
            let formData = new FormData
            formData.append("path",params.path);
            const res = await http.post(`${base.url.core}/test/file_asr`,formData);
            if (res.data) {                
                return res.data.result;
            }
        }
        catch (err) {
            return Promise.reject(err);
        }
    },
    async checkSetList(params) {
        try {
            const res = await http.post(`${base.url.core}/test/setting`,params);
            if (typeof res.data.result != "undefined") {                
                return res.data.result;
            }
        }
        catch (err) {
            return Promise.reject(err);
        }
    },
    
}
export default test;
